import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col } from 'antd';
import { floors } from 'dump/data';

const urlForm = '#floors/form';
const columns = [
  {
    title: 'Mã',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) => (
      <Fragment>
        <BtnEdit href={`${urlForm}/edit/${record.code}`} />
        <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
          <BtnDelete />
        </Popconfirm>
      </Fragment>
    ),
  },
];

class DormitoryBuilding extends Component {
  state = {};

  render() {
    return (
      <Card title="Tầng">
        <Row>
          <Col span={24}>
            <BtnAddNew href={`${urlForm}/new`} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={floors} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <DormitoryBuilding />
  </Page>
);



